.hidden {
    display: none!important;
}

.transparent {
	opacity: 0;
}

#delete-all-cont {
	display: flex;
	justify-content: flex-end;
	margin-top: 0px;
}

.boosted {
	margin-top: 16px!important;
}

#delete-all {
	justify-self: flex-start!important;
	align-self: flex-start!important;
}

footer {
	box-shadow: -1px 0px 8px 5px white;
	background-color: white;
	text-align: center;
	width: 40%;
	color: gray;
	font-family: 'Montserrat';
	display: flex;
	justify-content: center;
	align-content: center;
	position: fixed;
	padding: 20px 0px;
	bottom: 0;
}

@media screen and (max-width: 960px) {
	html {
	  width: 100%;
	}
	#delete-all-cont {
		width: 95%;
	}
	footer {
		width: 100%;
		font-size: 20%;
	}
}
